/*----------  Flat UI Color  ----------*/
@turquoise : #1abc9c;
@green-sea : #16a085;

@emerald : #2ecc71;
@nephritis : #27ae60;

@peter-river : #3498db;
@belize-hole : #2980b9;

@amethyst : #9b59b6;
@wisteria : #8e44ad;

@wet-asphalt : #34495e;
@midnight-blue : #2c3e50;

@sun-flower : #f1c40f;
@orange : #f39c12;

@carrot : #e67e22;
@pumpkin : #d35400;

@alizarin : #e74c3c;
@pomegranate : #c0392b;

@clouds : #f5f7f8;
@silver : #bdc3c7;

@concrete : #9aaaab;
@asbestos : #7f8c8d;

@flatColor: {
  turquoise: #1abc9c;
  green-sea: #16a085;
  emerald: #2ecc71;
  nephritis: #27ae60;
  peter-river: #3498db;
  belize-hole: #2980b9;
  amethyst: #9b59b6;
  wisteria: #8e44ad;
  wet-asphalt: #34495e;
  midnight-blue: #2c3e50;
  sun-flower: #f1c40f;
  orange: #f39c12;
  carrot: #e67e22;
  pumpkin: #d35400;
  alizarin: #e74c3c;
  pomegranate: #c0392b;
  clouds: #f5f7f8;
  silver: #bdc3c7;
  concrete: #9aaaab;
  asbestos: #7f8c8d;
}

each(@flatColor, {
  .text-@{key} {
    color: @value;
  }

  .bg-@{key} {
    background-color: @value;
  }
});
